	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 4 - L'entraînement","texte":{"childMarkdownRemark":{"html":"<p>Je me réveille. Mes avant-bras sont dans tes gantelets de force, m’empêchant de bouger les doigts et les poignets. J’ai un bandeau sur les yeux, qui m’empêche de voir autour de moi. J’entends, quelques secondes plus tard, suite à mon réveil, des bruits de pas qui s’approchent de moi.</p>\n<p>Darakei : On a essayé de s’enfuir, Monsieur Wamien?<br>\nWamien : Laissez-moi partir.<br>\nDarakei : Pas besoin d’être aussi agressif, on veut t’apprendre à bien utiliser ton corps pour te battre pour nous.<br>\nWamien : Pourquoi voulez-vous que je me batte pour vous?<br>\nDarakei : C’est un peu pour ça que nous t’avons modifié pour ton rôle dans notre société.<br>\nWamien : J’aurais quoi comme rôle?<br>\nDarakei : Une sorte de mercenaire.<br>\nWamien : Je vois... Et je fais comment pour finir mes études?<br>\nDarakei : Quand vous ne serez pas en mission, vous pourrez vivre normalement, mais si on vous demande, vous vous devez d’être disponible. De nuit comme de jour.<br>\nWamien : D’accord, j’ai compris. Je peux me faire retirer les gantelets, s’il vous plaît?</p>\n<p>Il s’approcha pour me détacher, me laissant libre de tous mouvements. Je souris, me dégourdis les jambes et les bras. Il me fit visiter le laboratoire, la salle de rencontres pour rédiger mes rapports en retour de missions. La piscine pour nager et relaxer, le spa, la salle de réparation si mon corps est endommagé pendant les missions. Et la fameuse salle d’entraînement. Un chef d’œuvre de la technologie robotique et scientifique. Des armes de toutes sortes se présentaient dans les dépôts, arme au corps à corps, arme à feu, arme blanche, pièce d’équipement de protection et tout autre gadget en cours de fabrication. Je n’avais pas vraiment envie de quitter cette pièce, Darakei pouvait certainement le sentir. Il me proposa d’y rester un peu et de m’y entraîner. Ce que j’acceptai curieux d’analyser le fonctionnement de tout cet arsenal.</p>\n<p>Je commence par regarder les grenades qui sont présentées à moi, il y a des grenades de plusieurs sortes, des smokes, des flashs, des semtex, des grenades normales, des grenages collantes qui envoient un rideau de glu qui se répend partout, malgré toutes ces possibilités, les grenades flash sont mes favorites.</p>\n<p>Je passe aux armes à longue distance, des snipers de toute sorte avec un nombre multiple de possibilités de montage. Après réflexion le sniper avec un zoom de 500 % et un silencieux me semble le plus avantageux en multiple situation.</p>\n<p>Maintenant les armes à feu. Mitraillette : trop de bruit. Shotgun : on doit être trop près. Je les passe tous un à un et finis par garder un simple pistolet. J’y ajoute un chargeur qui peut avoir trois fois plus de munitions et j’y ajoute un silencieux toujours aussi pratique.</p>\n<p>Pour les armes au corps, il y en a tellement que je ne saurais tous les énumérer, le gant américain reste le plus convenable et sera facilement intégrable à mon poing. Les dagues de 15 centimètres sont fabuleuses et très très faciles à envoyer sur les cibles.</p>\n<p>Finalement, les armes blanches, des sabres, des épées de toutes sortes, celle qui me plaît le plus est un sabre qui peut être ou non réfléchissant permettant d’aveugler l’ennemi. Sur la fiche, j’ai pu lire que le bout de la lame est en titane. Par contre, il y a un petit débalancement déplaisant dû au fait que le titane soit plus lourd que le manche ce qui peut être fatigant assez rapidement. J’ai aussi proposé de rajouter un poids équivalent au niveau du manche pour avoir un meilleur équilibre entre la lame et le manche.</p>"}},"chapitreAvant":"chapitre-3-la-fuite","chapitreApres":"chapitre-5-la-premiere-mission","nomRoman":"le-premier-cyborg-tome-1","slug":"chapitre-4-l-entrainement","codeChapitre":"H0001C004","equivalentUrl":"/stories/chapter/chapter-4-training"}},"pathContext":{"slug":"chapitre-4-l-entrainement"}}

